.slider-container {
  width: 300px;
  padding: 10px 0;
  margin: 5px;
}

/* Лейбл над слайдером */
.slider-label {
   display: block;
   margin-bottom: 1rem;
   font-size: 0.9rem;
   color: #333;
   font-weight: 500;
}

/* Контейнер для самого слайдера */
.slider {
  position: relative;
  height: 20px;
  display: flex;
  align-items: center;
}

/* Базовые стили для ползунка */
.slider__thumb,
.slider__thumb::-webkit-slider-thumb,
.slider__thumb::-moz-range-thumb {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  position: absolute;
  height: 16px;
  width: 16px;
  background-color: #007bff;
  border-radius: 50%;
  border: none;
  cursor: pointer;
  margin: 0;
  padding: 0;
  z-index: 3; /* Ползунок всегда сверху */
  pointer-events: none;
}

/* Стили для input (дорожка скрыта) */
.slider__thumb {
  pointer-events: auto;
  width: 100%;
  height: 4px;
  background: none;
  outline: none;
  /* Убираем z-index отсюда, т.к. только один input */
}

/* Webkit стили для ползунка */
.slider__thumb::-webkit-slider-thumb {
  pointer-events: auto;
  position: relative;
  margin-top: -6px;
}

/* Firefox стили для ползунка */
.slider__thumb::-moz-range-thumb {
  pointer-events: auto;
  position: relative;
}

/* Контейнер для визуальных дорожек */
.slider__track-container {
  position: absolute;
  top: 50%;
  left: 8px;
  right: 8px;
  height: 4px;
  transform: translateY(-50%);
  pointer-events: none;
  z-index: 1;
}

/* Основная серая дорожка */
.slider__track {
  position: absolute;
  height: 100%;
  width: 100%;
  background-color: #dee2e6;
  border-radius: 3px;
}

/* Выделенный диапазон (от начала до ползунка) */
.slider__range {
  position: absolute;
  height: 100%;
  background-color: #007bff;
  border-radius: 3px;
  z-index: 2;
}

/* Деления на дорожке */
.slider__tick {
    position: absolute;
    width: 1px;
    height: 8px; /* Высота деления */
    background-color: rgba(0, 0, 0, 0.2); /* Цвет деления */
    top: 50%;
    transform: translate(-50%, -50%); /* Центрируем деление */
    z-index: 1; /* Под ползунком, над серой дорожкой */
}


/* Отображение текущего значения */
.slider-value {
  text-align: center; /* Центрируем значение */
  margin-top: 8px;
  font-size: 0.85rem;
  color: #6c757d;
}

/* Стили для отключенного состояния */
.slider--disabled {
    opacity: 0.6;
}
.slider--disabled .slider__thumb,
.slider--disabled .slider__thumb::-webkit-slider-thumb,
.slider--disabled .slider__thumb::-moz-range-thumb {
    cursor: not-allowed;
    background-color: #adb5bd;
}
.slider--disabled .slider__range {
    background-color: #adb5bd;
}
.slider--disabled .slider__tick {
     background-color: rgba(0, 0, 0, 0.1);
} `, "",{"version":3,"sources":["webpack://./src/components/Slider/Slider.css"],"names":[],"mappings":"AAAA,oBAAoB;AACpB;EACE,YAAY;EACZ,eAAe;EACf,WAAW;AACb;;AAEA,wBAAwB;AACxB;GACG,cAAc;GACd,mBAAmB;GACnB,iBAAiB;GACjB,WAAW;GACX,gBAAgB;AACnB;;AAEA,kCAAkC;AAClC;EACE,kBAAkB;EAClB,YAAY;EACZ,aAAa;EACb,mBAAmB;AACrB;;AAEA,+BAA+B;AAC/B;;;EAGE,wBAAwB;EACxB,qBAAqB;EACrB,gBAAgB;EAChB,kBAAkB;EAClB,YAAY;EACZ,WAAW;EACX,yBAAyB;EACzB,kBAAkB;EAClB,YAAY;EACZ,eAAe;EACf,SAAS;EACT,UAAU;EACV,UAAU,EAAE,2BAA2B;EACvC,oBAAoB;AACtB;;AAEA,qCAAqC;AACrC;EACE,oBAAoB;EACpB,WAAW;EACX,WAAW;EACX,gBAAgB;EAChB,aAAa;EACb,mDAAmD;AACrD;;AAEA,8BAA8B;AAC9B;EACE,oBAAoB;EACpB,kBAAkB;EAClB,gBAAgB;AAClB;;AAEA,+BAA+B;AAC/B;EACE,oBAAoB;EACpB,kBAAkB;AACpB;;AAEA,qCAAqC;AACrC;EACE,kBAAkB;EAClB,QAAQ;EACR,SAAS;EACT,UAAU;EACV,WAAW;EACX,2BAA2B;EAC3B,oBAAoB;EACpB,UAAU;AACZ;;AAEA,2BAA2B;AAC3B;EACE,kBAAkB;EAClB,YAAY;EACZ,WAAW;EACX,yBAAyB;EACzB,kBAAkB;AACpB;;AAEA,gDAAgD;AAChD;EACE,kBAAkB;EAClB,YAAY;EACZ,yBAAyB;EACzB,kBAAkB;EAClB,UAAU;AACZ;;AAEA,uBAAuB;AACvB;IACI,kBAAkB;IAClB,UAAU;IACV,WAAW,EAAE,mBAAmB;IAChC,oCAAoC,EAAE,iBAAiB;IACvD,QAAQ;IACR,gCAAgC,EAAE,uBAAuB;IACzD,UAAU,EAAE,sCAAsC;AACtD;;;AAGA,kCAAkC;AAClC;EACE,kBAAkB,EAAE,wBAAwB;EAC5C,eAAe;EACf,kBAAkB;EAClB,cAAc;AAChB;;AAEA,qCAAqC;AACrC;IACI,YAAY;AAChB;AACA;;;IAGI,mBAAmB;IACnB,yBAAyB;AAC7B;AACA;IACI,yBAAyB;AAC7B;AACA;KACK,oCAAoC;AACzC","sourcesContent":["/* Общий контейнер */\n.slider-container {\n  width: 300px;\n  padding: 10px 0;\n  margin: 5px;\n}\n\n/* Лейбл над слайдером */\n.slider-label {\n   display: block;\n   margin-bottom: 1rem;\n   font-size: 0.9rem;\n   color: #333;\n   font-weight: 500;\n}\n\n/* Контейнер для самого слайдера */\n.slider {\n  position: relative;\n  height: 20px;\n  display: flex;\n  align-items: center;\n}\n\n/* Базовые стили для ползунка */\n.slider__thumb,\n.slider__thumb::-webkit-slider-thumb,\n.slider__thumb::-moz-range-thumb {\n  -webkit-appearance: none;\n  -moz-appearance: none;\n  appearance: none;\n  position: absolute;\n  height: 16